.carrinho-alert-background {
  width: 100vw;
  height: 100vh;
  z-index: 998;
  background-color: rgba(0, 0, 0, .54);
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.carrinho-alert {
  width: 50vw;
  height: 0;
  background-color: var(--branco);
  z-index: 999;
  border: 1px solid #000;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  align-items: center;
  padding: 10px;
  transition: all .8s;
  display: none;
  position: absolute;
  top: calc(50% - 40vh);
  left: calc(50% - 25vw);
  overflow: auto;
}

.carrinho-alert__confere {
  box-shadow: 1px 1px 8px 3px var(--shadow);
  max-width: 40%;
  max-height: 40%;
  background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTjuRgmqRR4U-fUyGU1Xf-Y1IEnInqqVtxm9w&usqp=CAU");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%;
  flex-shrink: 2;
  margin-bottom: 10vh;
  padding: 20%;
  -webkit-animation: 1s bong;
  animation: 1s bong;
  display: inline-block;
}

.carrinho-alert__botao {
  background-color: var(--botao-base);
  border: 2px solid var(--preto);
  color: var(--branco);
  border-radius: 15px;
  flex-shrink: 2;
  margin: 0 5px;
  padding: 12px;
  font-size: 1.15rem;
  display: inline;
}

.carrinho-alert__texto {
  text-align: center;
  width: 99%;
  display: block;
}

.carrinho-alert__quantidade {
  width: 100%;
  text-align: center;
  margin: 8px 0;
  font-size: 1.75rem;
  display: block;
}

.carrinho-alert__quantidade #quantidade_ramens {
  width: 40px;
  height: 1.75rem;
}

@-webkit-keyframes bong {
  0% {
    -webkit-transform: scale(.4);
    transform: scale(.4);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes bong {
  0% {
    -webkit-transform: scale(.4);
    transform: scale(.4);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

